<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        v-model="listQuery.CompanyName"
        placeholder="关键词"
        style="width: 200px"
        clearable=""
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-select
        v-model="listQuery.Status"
        placeholder="状态"
        clearable
        style="width: 90px"
        class="filter-item"
      >
        <el-option
          v-for="(item, index) in statusOptions"
          :key="index"
          :label="item"
          :value="index"
        />
      </el-select>
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        查询
      </el-button>
      <el-button
        v-waves
        class="filter-item"
        style="margin-left: 10px"
        type="primary"
        icon="el-icon-edit"
        @click="handleCreate"
      >
        新增
      </el-button>
      <el-button
        v-waves
        :loading="downloadLoading"
        class="filter-item"
        type="primary"
        icon="el-icon-download"
        @click="handleDownload"
      >
        导出
      </el-button>
    </div>

    <el-table
      :key="tableKey"
      v-loading="listLoading"
      :data="list"
      border
      fit
      highlight-current-row
      style="width: 100%"
      height="72vh"
      :header-cell-style="{ 'background-color': 'rgba(245,245,245)' }"
      @sort-change="sortChange"
    >
      <el-table-column label="序号" type="index" align="center" width="80">
      </el-table-column>
      <el-table-column label="单位名称" min-width="100px">
        <template slot-scope="{ row }">
          <span class="link-type" @click="handleAction(row, 'detail')">{{
            row.CompanyName
          }}</span>
        </template>
      </el-table-column>
      <el-table-column label="注册地址">
        <template slot-scope="{ row }">
          <span>{{ row.RegisteredAddr }}</span>
        </template>
      </el-table-column>
      <el-table-column label="联系人" width="110px" align="center">
        <template slot-scope="{ row }">
          <div>{{ row.Contacts }}</div>
          <div>{{ row.Contactstel }}</div>
        </template>
      </el-table-column>
      <el-table-column label="在监项目个数" align="center" width="115">
        <template slot-scope="{ row }">
          <span
            v-if="row.ProjectCount"
            class="link-type"
            @click="handleFetchProject(row.Id)"
            >{{ row.ProjectCount }}</span
          >
          <span v-else>0</span>
        </template>
      </el-table-column>
      <el-table-column label="监理人数" align="center" width="95">
        <template slot-scope="{ row }">
          <span
            v-if="row.SupervisorCount"
            class="link-type"
            @click="handleFetchPersonnel(row.Id)"
            >{{ row.SupervisorCount }}</span
          >
          <span v-else>0</span>
        </template>
      </el-table-column>
      <el-table-column label="状态" class-name="status-col" width="100">
        <template slot-scope="{ row }">
          <el-tag :type="row.Status | statusFilter">
            {{ statusOptions[row.Status] }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="230"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row }">
          <el-button
            type="primary"
            size="mini"
            @click="handleAction(row, 'update')"
          >
            编辑
          </el-button>
          <el-button
            v-if="row.Status != '1'"
            size="mini"
            type="warning"
            @click="handleModifyStatus(row, '1')"
          >
            暂停
          </el-button>
          <el-button
            v-if="row.Status != '0'"
            size="mini"
            type="success"
            @click="handleModifyStatus(row, '0')"
          >
            恢复
          </el-button>
          <el-button
            v-if="row.Status != '已删除'"
            size="mini"
            type="danger"
            @click="handleDelete(row)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="listQuery.PageIndex"
      :limit.sync="listQuery.PageSize"
      @pagination="getList"
    />

    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <!-- <company :dialog-status="dialogStatus" :temp="temp"></company> -->
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="temp"
        label-position="left"
        label-width="110px"
      >
        <el-row :gutter="0" style="width: 100%">
          <el-col :span="10" :offset="1">
            <el-form-item label="单位名称" prop="CompanyName">
              <el-input
                v-model="temp.CompanyName"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item label="组织机构代码" prop="OrganizationCode">
              <el-input
                v-model="temp.OrganizationCode"
                :readonly="dialogStatus === 'detail'"
                maxlength="9"
                show-word-limit
              />
            </el-form-item>
            <el-form-item label="注册地址" prop="RegisteredAddr">
              <el-input
                v-model="temp.RegisteredAddr"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item label="法人" prop="LegalPerson">
              <el-input
                v-model="temp.LegalPerson"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item label="法人联系方式" prop="Tel">
              <el-input
                v-model="temp.Tel"
                :readonly="dialogStatus === 'detail'"
                maxlength="11"
                show-word-limit
              />
            </el-form-item>
            <el-form-item label="联系人" prop="Contacts">
              <el-input
                v-model="temp.Contacts"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item label="联系方式" prop="Contactstel">
              <el-input
                v-model="temp.Contactstel"
                :readonly="dialogStatus === 'detail'"
                maxlength="11"
                show-word-limit
              />
            </el-form-item>
          </el-col>
          <el-col :span="10" :offset="2" class="upload-container">
            <el-form-item label="资质证书" prop="Certificate">
              <el-upload
                :disabled="dialogStatus === 'detail'"
                class="avatar-uploader"
                list-type="picture-card"
                :action="uploadUrl"
                ref="certificateUploader"
                name="file"
                :limit="1"
                :headers="headers"
                :show-file-list="false"
                :auto-upload="true"
                :on-preview="handlePreview"
                :on-success="handleCertificationSuccess"
                :on-error="handleCertificationError"
                :on-change="handleCertificationChange"
                :before-upload="beforeAvatarUpload"
                :before-remove="beforeRemove"
                :on-remove="handleRemove"
                :on-exceed="handleExceed"
              >
                <el-image
                  v-if="temp.CertificateUrl"
                  :src="temp.CertificateUrl"
                  class="avatar"
                >
                  <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline"></i>
                  </div>
                </el-image>
                <i
                  v-else-if="dialogStatus === 'detail'"
                  class="el-icon-picture-outline avatar-uploader-icon"
                ></i>
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>

                <div
                  v-if="dialogStatus !== 'detail'"
                  slot="tip"
                  class="el-upload__tip"
                >
                  只能上传不超过5M的jpg图片
                </div>
              </el-upload>
            </el-form-item>
            <el-form-item label="营业执照" prop="License">
              <el-upload
                :disabled="dialogStatus === 'detail'"
                class="avatar-uploader"
                list-type="picture-card"
                :action="uploadUrl"
                ref="licenseUploader"
                name="file"
                :limit="1"
                :headers="headers"
                :show-file-list="false"
                :auto-upload="true"
                :on-preview="handlePreview"
                :on-success="handleBusinessLicenseSuccess"
                :on-error="handleBusinessLicenseError"
                :on-change="handleBusinessLicenseChange"
                :before-upload="beforeAvatarUpload"
                :before-remove="beforeRemove"
                :on-remove="handleRemove"
                :on-exceed="handleExceed"
              >
                <el-image
                  v-if="temp.LicenseUrl"
                  :src="temp.LicenseUrl"
                  class="avatar"
                >
                  <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline"></i>
                  </div>
                </el-image>
                <i
                  v-else-if="dialogStatus === 'detail'"
                  class="el-icon-picture-outline avatar-uploader-icon"
                ></i>
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                <div
                  v-if="dialogStatus !== 'detail'"
                  slot="tip"
                  class="el-upload__tip"
                >
                  只能上传不超过5M的jpg图片
                </div>
              </el-upload>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div v-if="dialogStatus !== 'detail'" slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> 取消 </el-button>
        <el-button
          type="primary"
          @click="dialogStatus === 'create' ? createData() : updateData()"
        >
          确认
        </el-button>
      </div>
    </el-dialog>

    <el-dialog
      :visible.sync="dialogPersonnelVisible"
      title="监理人员"
      width="70%"
    >
      <el-table
        :data="personnelData"
        border
        fit
        highlight-current-row
        height="50vh"
        style="width: 100%"
      >
        <el-table-column type="index" label="序号" width="60" align="center" />
        <el-table-column prop="Name" label="姓名" width="90" align="center" />
        <el-table-column prop="PostCode" label="岗位" width="60" align="center">
          <template slot-scope="{ row }">
            <span>{{ getPostName(row.PostCode) }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="CardId"
          label="身份证号"
          width="170"
          align="center"
        />
        <el-table-column
          prop="Tel"
          label="联系方式"
          width="120"
          align="center"
        />
        <el-table-column prop="ProjectName" label="所属项目" />
        <el-table-column
          prop="CertificateStatusName"
          label="证书状态"
          width="90"
          align="center"
        >
          <template slot-scope="{ row }">
            <el-tag :type="row.CertificateStatusName | certificateStatusFilter">
              {{ row.CertificateStatusName }}
            </el-tag>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogPersonnelVisible = false"
          >确定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      :visible.sync="dialogProjectVisible"
      title="在监项目"
      width="70%"
    >
      <el-table
        :data="projectData"
        border
        fit
        highlight-current-row
        height="50vh"
        style="width: 100%"
      >
        <el-table-column type="index" label="序号" width="60" align="center" />
        <el-table-column prop="ProjName" label="项目名称" align="center" />
        <el-table-column label="项目类别" width="150" align="center">
          <template slot-scope="{ row }">
            <span>{{ getProjectTypeName(row.ProjectTypeId) }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="PhaseName"
          label="项目阶段"
          width="80"
          align="center"
        />
        <el-table-column label="项目状态" width="90" align="center">
          <template slot-scope="{ row }">
            <el-tag :type="row.StatusName | projectStatusFilter">
              {{ row.StatusName }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="项目负责人" width="100" align="center">
          <template slot-scope="{ row }">
            <span>{{ row.Contact }}</span>
          </template>
        </el-table-column>
        <el-table-column label="所属区域"
          ><template slot-scope="{ row }">
            {{ row.ProProvince + "/" + row.ProCity + "/" + row.ProjArea }}
          </template></el-table-column
        >
        <el-table-column label="地点" align="center">
          <template slot-scope="{ row }">
            <span>{{ row.ProjAddr }}</span>
          </template>
        </el-table-column>
        <el-table-column label="监理人数" width="90" align="center">
          <template slot-scope="{ row }">
            <span>{{ row.SupervisorCount }}</span>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogProjectVisible = false"
          >确定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog :visible.sync="dialogVisible" title="">
      <el-image :src="dialogImageUrl">
        <div slot="error" class="image-slot">
          <i class="el-icon-picture-outline"></i>
        </div>
      </el-image>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { baseURL } from "@/utils/request";
import { validPhoneNumber, validOrganizationCode } from "@/utils/validate";
import { getToken } from "@/utils/auth";
import { fetchPageList, saveCompany, deleteCompany } from "@/api/company";
import { fetchList as fetchPersonnel } from "@/api/personnel";
import { fetchList as fetchProject } from "@/api/project";
import { uploadImg, saveImg, getFileUrl } from "@/api/file";
import waves from "@/directive/waves"; // waves directive
import { parseTime } from "@/utils";
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import Company from "./components/company.vue";

export default {
  name: "CompanyIndex",
  components: { Pagination, Company },
  directives: { waves },
  filters: {
    projectStatusFilter(Status) {
      const statusMap = {
        未开始: "info",
        进行中: "success",
        已结束: "",
      };
      return statusMap[Status];
    },
    statusFilter(Status) {
      const statusTypes = ["success", "info"];
      return statusTypes[Status];
    },
    certificateStatusFilter(Status) {
      const statusMap = {
        正常: "success",
        已过期: "danger",
      };
      return statusMap[Status];
    },
  },
  computed: {
    ...mapGetters(["dicts", "projectTypes"]),
  },
  data() {
    return {
      Posts: [],
      headers: {
        // "Content-Type": "multipart/form-data",
        Authorization: "Bearer " + getToken(),
      },
      dialogImageUrl: "",
      dialogVisible: false,
      uploadUrl: "",
      tableKey: 0,
      list: null,
      total: 0,
      listLoading: true,
      listQuery: {
        CompanyName: "",
        PageIndex: 1,
        PageSize: 20,
        Status: "",
      },
      statusOptions: ["正常", "已暂停"],
      sortOptions: [
        { label: "ID 升序", key: "+id" },
        { label: "ID 降序", key: "-id" },
      ],
      temp: {
        CompanyName: "",
        OrganizationCode: "",
        RegisteredAddr: "",
        LegalPerson: "",
        Tel: "",
        Certificate: "",
        CertificateUrl: "",
        License: "",
        LicenseUrl: "",
        Contacts: "",
        Contactstel: "",
        Status: 0,
        ProjectCount: 0,
        SupervisorCount: 0,
        Id: 0,
      },
      dialogFormVisible: false,
      dialogStatus: "",
      textMap: {
        update: "编辑",
        create: "新增",
        detail: "监理单位详情",
      },
      dialogPersonnelVisible: false,
      dialogProjectVisible: false,
      personnelData: [],
      projectData: [],
      rules: {
        CompanyName: [
          { required: true, message: "单位名称为必填项", trigger: "blur" },
        ],
        OrganizationCode: [
          {
            required: true,
            message: "组织机构代码格式不正确。",
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value && !validOrganizationCode(value)) {
                callback(new Error("组织机构代码格式不正确。"));
              } else {
                callback();
              }
            },
          },
        ],
        RegisteredAddr: [
          { required: true, message: "注册地址为必填项", trigger: "blur" },
        ],
        Contacts: [
          { required: true, message: "联系人为必填项", trigger: "blur" },
        ],
        Contactstel: [
          {
            required: true,
            message: "手机号格式不正确",
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value && !validPhoneNumber(value)) {
                callback(new Error("请输入正确的手机号"));
              } else {
                callback();
              }
            },
          },
        ],
        LegalPerson: [
          { required: true, message: "法人为必填项", trigger: "blur" },
        ],
        Certificate: [
          { required: true, message: "资质证书必须上传", trigger: "blur" },
        ],
        License: [
          { required: true, message: "营业执照必须上传", trigger: "blur" },
        ],
        Tel: [
          {
            required: true,
            message: "手机号格式不正确",
            trigger: "blur",
            validator: (rule, value, callback) => {
              if (value && !validPhoneNumber(value)) {
                callback(new Error("请输入正确的手机号"));
              } else {
                callback();
              }
            },
          },
        ],
      },
      downloadLoading: false,
    };
  },
  created() {
    this.uploadUrl = baseURL + "/File/UploadImg";
    let obj = this.dicts.find((dict) => dict.DictType == "Post");
    if (obj) {
      this.Posts = obj.Detail;
    }
    this.getList();
  },
  methods: {
    getProjectTypeName(typeId) {
      let projectType = this.projectTypes.find((_) => {
        return _.Id == typeId;
      });
      return projectType.Name;
    },
    getPostName(postCode) {
      let post = this.Posts.find((post) => post.DictKey == postCode);
      return post ? post.DictValue : "";
    },
    getList() {
      this.listLoading = true;
      fetchPageList(this.listQuery)
        .then((response) => {
          this.list = response.Data;
          this.total = response.Total;

          this.listLoading = false;
        })
        .catch((error) => {
          this.listLoading = false;
        });
    },
    handleFilter() {
      this.listQuery.PageIndex = 1;
      this.getList();
    },
    handleModifyStatus(row, Status) {
      const tempData = Object.assign({}, row);
      tempData.Status = Status;
      tempData.timestamp = +new Date(tempData.timestamp); // change Thu Nov 30 2017 16:41:05 GMT+0800 (CST) to 1512031311464
      saveCompany(tempData)
        .then(() => {
          this.getList();
          this.$notify({
            title: "成功",
            message: "操作成功",
            type: "success",
            duration: 2000,
          });
        })
        .catch((error) => {
          this.$message({
            title: "失败",
            message: "操作失败",
            type: "error",
          });
        });
    },
    sortChange(data) {
      const { prop, order } = data;
      if (prop === "id") {
        this.sortByID(order);
      }
    },
    sortByID(order) {
      if (order === "ascending") {
        this.listQuery.sort = "+id";
      } else {
        this.listQuery.sort = "-id";
      }
      this.handleFilter();
    },
    resetTemp() {
      this.temp = {
        CompanyName: "",
        OrganizationCode: "",
        RegisteredAddr: "",
        LegalPerson: "",
        Tel: "",
        Certificate: "",
        CertificateUrl: "",
        License: "",
        LicenseUrl: "",
        Contacts: "",
        Contactstel: "",
        Status: 0,
        ProjectCount: 0,
        SupervisorCount: 0,
        Id: 0,
      };
    },
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    saveImg(temp) {
      if (
        !temp.createSavedImg &&
        temp.CertificateFileId &&
        temp.LicenseFileId
      ) {
        return new Promise((resolve, reject) => {
          Promise.all([
            saveImg({
              Id: 0,
              FileId: temp.CertificateFileId,
              FileType: 2,
              FileName: temp.CertificateFileName,
              FileSize: temp.CertificateFileSize,
            }),
            saveImg({
              Id: 0,
              FileId: temp.LicenseFileId,
              FileType: 2,
              FileName: temp.LicenseFileName,
              FileSize: temp.LicenseFileSize,
            }),
          ])
            .then((result) => {
              console.log("准备工作完毕12");
              console.log(result);
              temp.Certificate = result[0].Data;
              temp.License = result[1].Data;
              temp.createSavedImg = true;
              resolve(temp);
            })
            .catch(() => {
              reject();
            });
        });
      } else if (
        !temp.createSavedImg &&
        temp.CertificateFileId &&
        !temp.LicenseFileId
      ) {
        return new Promise((resolve, reject) => {
          saveImg({
            Id: 0,
            FileId: temp.CertificateFileId,
            FileType: 2,
            FileName: temp.CertificateFileName,
            FileSize: temp.CertificateFileSize,
          })
            .then((result) => {
              console.log("准备工作完毕1");
              console.log(result);
              temp.Certificate = result.Data;
              temp.createSavedImg = true;
              resolve(temp);
            })
            .catch(() => {
              reject();
            });
        });
      } else if (
        !temp.createSavedImg &&
        !temp.CertificateFileId &&
        temp.LicenseFileId
      ) {
        return new Promise((resolve, reject) => {
          saveImg({
            Id: 0,
            FileId: temp.LicenseFileId,
            FileType: 2,
            FileName: temp.LicenseFileName,
            FileSize: temp.LicenseFileSize,
          })
            .then((result) => {
              console.log("准备工作完毕2");
              console.log(result);
              temp.License = result.Data;
              temp.createSavedImg = true;
              resolve(temp);
            })
            .catch(() => {
              reject();
            });
        });
      } else {
        return new Promise((resolve, reject) => {
          console.log("准备工作完毕0");
          resolve(temp);
        });
      }
    },
    createData() {
      this.saveImg(this.temp).then((res) => {
        this.$refs["dataForm"].validate((valid) => {
          if (valid) {
            let {
              CertificateFileId,
              CertificateFileName,
              CertificateFileSize,
              CertificateUrl,
              LicenseFileId,
              LicenseFileName,
              LicenseFileSize,
              LicenseUrl,
              ...temp
            } = res;
            saveCompany(temp)
              .then(() => {
                this.getList();
                this.dialogFormVisible = false;
                this.$notify({
                  title: "成功",
                  message: "新增成功！",
                  type: "success",
                  duration: 2000,
                });
              })
              .catch((error) => {
                this.$notify({
                  title: "失败",
                  message: "新增失败！",
                  type: "error",
                  duration: 2000,
                });
              });
          }
        });
      });
    },
    handleAction(row, type) {
      let data = { IdS: `${row.Certificate},${row.License}` };
      getFileUrl(data)
        .then((response) => {
          let data = response.Data;
          this.temp = Object.assign({}, row); // copy obj
          if (data && data.length > 0) {
            let CertificateUrl = data.find((_) => _.Id == row.Certificate)
              .FileId;
            this.$set(
              this.temp,
              "CertificateUrl",
              baseURL + "/" + CertificateUrl
            );
            let LicenseUrl = data.find((_) => _.Id == row.License).FileId;
            this.$set(this.temp, "LicenseUrl", baseURL + "/" + LicenseUrl)
              .FileId;
          }
          this.dialogStatus = type;
          this.dialogFormVisible = true;
          this.$nextTick(() => {
            this.$refs["dataForm"].clearValidate();
          });
        })
        .catch();
    },
    updateData() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          let temp = Object.assign({}, this.temp);
          this.saveImg(temp).then((res) => {
            saveCompany(res)
              .then(() => {
                this.getList();
                this.dialogFormVisible = false;
                this.$notify({
                  title: "成功",
                  message: "操作成功！",
                  type: "success",
                  duration: 2000,
                });
              })
              .catch((error) => {
                this.$notify({
                  title: "失败",
                  message: "操作失败！",
                  type: "error",
                  duration: 2000,
                });
              });
          });
        }
      });
    },
    handleCertificationUpload(params) {
      let param = new FormData();
      param.append("files", params.file);
      alert(param);
      uploadImg(param).then((res) => {
        this.temp.Certificate = [];
        this.temp.Certificate.push(res.data.data);
      });
    },
    handleBusinessLicenseUpload(params) {
      let param = new FormData();
      param.append("files", params.file);
      alert(param);
      uploadImg(param).then((res) => {
        this.temp.License = [];
        this.temp.License.push(res.data.data);
      });
    },
    handlePreview(file) {
      console.log(file);
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleCertificationSuccess(res, file) {
      this.temp.CertificateUrl = URL.createObjectURL(file.raw);
      this.temp.CertificateFileId = res.Data;
      this.temp.CertificateFileName = file.name;
      this.temp.CertificateFileSize = file.size;
      this.$refs.certificateUploader.clearFiles();
      console.log("handleCertificationSuccess.res", res);
      console.log("handleCertificationSuccess.file", file);
      console.log("temp", this.temp);
    },
    handleBusinessLicenseSuccess(res, file) {
      this.temp.LicenseUrl = URL.createObjectURL(file.raw);
      this.temp.LicenseFileId = res.Data;
      this.temp.LicenseFileName = file.name;
      this.temp.LicenseFileSize = file.size;
      this.$refs.licenseUploader.clearFiles();
      console.log("handleBusinessLicenseSuccess.res", res);
      console.log("handleBusinessLicenseSuccess.file", file);
      console.log("temp", this.temp);
    },
    handleCertificationError(err, file, fileList) {
      // this.temp.contractUrl = URL.createObjectURL(file.raw);
    },
    handleBusinessLicenseError(err, file, fileList) {
      // this.temp.contractUrl = URL.createObjectURL(file.raw);
    },
    handleCertificationChange(file, fileList) {
      // this.headFile = file;
      // this.headFileList = fileList;
      // if (fileList.length == 1) {
      //   let { contractUrl, ...data } = this.baseDataRules;
      //   this.baseDataRules = data;
      // }
      // this.$refs.baseDataForm.clearValidate("contractUrl");
      // this.hideUpload = fileList.length >= 1;
    },
    handleBusinessLicenseChange(file, fileList) {
      // this.headFile = file;
      // this.headFileList = fileList;
      // if (fileList.length == 1) {
      //   let { contractUrl, ...data } = this.baseDataRules;
      //   this.baseDataRules = data;
      // }
      // this.$refs.baseDataForm.clearValidate("contractUrl");
      // this.hideUpload = fileList.length >= 1;
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    beforeRemove(file, fileList) {
      // return this.$confirm(`确定移除 ${file.name}？`);
      return true;
    },
    handleRemove(files, fileList) {
      console.log(file, fileList);
      if (fileList.length == 0) {
        this.baseDataRules.contractUrl = [
          { required: true, message: "请上传合同", trigger: "change" },
        ];
      }
      this.hideUpload = fileList.length >= 1;
    },
    handleExceed(file, fileList) {
      console.log(file, fileList);
    },
    handleDelete(row) {
      this.$confirm("此操作将删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then((_) => {
          deleteCompany({ Ids: row.Id })
            .then((response) => {
              this.$notify({
                title: "成功",
                message: "删除成功",
                type: "success",
                duration: 2000,
              });
              this.getList();
            })
            .catch((error) => {
              this.$notify({
                title: "失败",
                message: "删除失败！",
                type: "error",
                duration: 2000,
              });
            });
        })
        .catch((_) => {});
    },
    handleFetchPersonnel(id) {
      fetchPersonnel({ SuperviseId: id }).then((response) => {
        this.personnelData = response.Data;
        this.dialogPersonnelVisible = true;
      });
    },
    handleFetchProject(id) {
      fetchProject({ SuperviseComId: id }).then((response) => {
        this.projectData = response.Data;
        this.dialogProjectVisible = true;
      });
    },
    handleDownload() {
      this.downloadLoading = true;
      import("@/vendor/Export2Excel").then((excel) => {
        const tHeader = [
          "单位名称",
          "注册地址",
          "联系人",
          "在监项目数",
          "监理人数",
          "状态",
        ];
        const filterVal = [
          "CompanyName",
          "RegisteredAddr",
          "Contacts",
          "ProjectCount",
          "SupervisorCount",
          "Status",
        ];
        const data = this.formatJson(filterVal);
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: "监理单位列表",
        });
        this.downloadLoading = false;
      });
    },
    formatJson(filterVal) {
      return this.list.map((v) =>
        filterVal.map((j) => {
          if (j === "Status") {
            return this.statusOptions[v[j]];
          } else {
            return v[j];
          }
        })
      );
    },
    getSortClass: function (key) {
      const sort = this.listQuery.sort;
      return sort === `+${key}` ? "ascending" : "descending";
    },
  },
};
</script>
