import {request} from "@/utils/request";

export function fetchList(query) {
  return request({
    url: "/SuperviseCompany/GetList",
    method: "get",
    params: query
  });
}

export function fetchPageList(query) {
  return request({
    url: "/SuperviseCompany/GetPageList",
    method: "get",
    params: query
  });
}

export function fetchCompany(id) {
  return request({
    url: "/SuperviseCompany/GetForm",
    method: "get",
    params: { id }
  });
}

export function saveCompany(data) {
  return request({
    url: "/SuperviseCompany/SaveForm",
    method: "post",
    data
  });
}
export function deleteCompany(data) {
  return request({
    url: "/SuperviseCompany/DeleteForm",
    method: "post",
    data
  });
}
