<template>
  <div>
    <el-form
      ref="dataForm"
      :rules="rules"
      :model="temp"
      label-position="left"
      label-width="110px"
    >
      <el-row :gutter="0" style="width: 100%">
        <el-col :span="13" :offset="1">
          <el-form-item label="单位名称" prop="CompanyName">
            <el-input
              v-model="temp.CompanyName"
              :readonly="dialogStatus === 'detail'"
            />
          </el-form-item>
          <el-form-item label="组织机构代码" prop="OrganizationCode">
            <el-input
              v-model="temp.OrganizationCode"
              :readonly="dialogStatus === 'detail'"
            />
          </el-form-item>
          <el-form-item label="注册地址" prop="RegisteredAddr">
            <el-input
              v-model="temp.RegisteredAddr"
              :readonly="dialogStatus === 'detail'"
            />
          </el-form-item>
          <el-form-item label="法人" prop="LegalPerson">
            <el-input
              v-model="temp.LegalPerson"
              :readonly="dialogStatus === 'detail'"
            />
          </el-form-item>
          <el-form-item label="法人联系方式" prop="Tel">
            <el-input
              v-model="temp.Tel"
              :readonly="dialogStatus === 'detail'"
            />
          </el-form-item>
          <el-form-item label="联系人" prop="Contacts">
            <el-input
              v-model="temp.Contacts"
              :readonly="dialogStatus === 'detail'"
            />
          </el-form-item>
          <el-form-item label="联系方式" prop="Contactstel">
            <el-input
              v-model="temp.Contactstel"
              :readonly="dialogStatus === 'detail'"
            />
          </el-form-item>
        </el-col>
        <el-col :span="8" :offset="1" class="upload-container">
          <el-form-item label="资质证书" prop="Certificate">
            <el-upload
              :disabled="dialogStatus === 'detail'"
              class="avatar-uploader"
              :action="uploadUrl"
              name="file"
              limit="1"
              :headers="headers"
              :show-file-list="false"
              :auto-upload="true"
              :on-preview="handlePreview"
              :on-success="handleCertificationSuccess"
              :on-error="handleCertificationError"
              :on-change="handleCertificationChange"
              :before-upload="beforeAvatarUpload"
              :before-remove="beforeRemove"
              :on-remove="handleRemove"
              :on-exceed="handleExceed"
            >
              <img v-if="Certificate" :src="Certificate" class="avatar" />
              <i
                v-if="dialogStatus !== 'detail'"
                class="el-icon-plus avatar-uploader-icon"
              ></i>
              <i
                v-if="dialogStatus === 'detail'"
                class="el-icon-picture-outline avatar-uploader-icon"
              ></i>
              <div
                v-if="dialogStatus !== 'detail'"
                slot="tip"
                class="el-upload__tip"
              >
                只能上传不超过5M的jpg图片
              </div>
            </el-upload>
          </el-form-item>
          <el-form-item label="营业执照" prop="License">
            <el-upload
              :disabled="dialogStatus === 'detail'"
              class="avatar-uploader"
              :action="uploadUrl"
              name="file"
              limit="1"
              :headers="headers"
              :show-file-list="false"
              :auto-upload="true"
              :on-preview="handlePreview"
              :on-success="handleBusinessLicenseSuccess"
              :on-error="handleBusinessLicenseError"
              :on-change="handleBusinessLicenseChange"
              :before-upload="beforeAvatarUpload"
              :before-remove="beforeRemove"
              :on-remove="handleRemove"
              :on-exceed="handleExceed"
            >
              <img v-if="License" :src="License" class="avatar" />
              <i
                v-if="dialogStatus !== 'detail'"
                class="el-icon-plus avatar-uploader-icon"
              ></i>
              <i
                v-if="dialogStatus === 'detail'"
                class="el-icon-picture-outline avatar-uploader-icon"
              ></i>
              <div
                v-if="dialogStatus !== 'detail'"
                slot="tip"
                class="el-upload__tip"
              >
                只能上传不超过5M的jpg图片
              </div>
            </el-upload>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel"> 取消 </el-button>
      <el-button type="primary" @click="sure"> 确认 </el-button>
    </div>
  </div>
</template>

<script>
import { saveCompany } from "@/api/company";
export default {
  props: {
    dialogStatus: {
      type: String,
      default: "",
    },
    temp: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      rules: {
        timestamp: [
          {
            type: "date",
            required: true,
            message: "timestamp is required",
            trigger: "change",
          },
        ],
        CompanyName: [
          { required: true, message: "单位名称为必填项", trigger: "blur" },
        ],
        OrganizationCode: [
          { required: true, message: "组织机构代码为必填项", trigger: "blur" },
        ],
        RegisteredAddr: [
          { required: true, message: "注册地址为必填项", trigger: "blur" },
        ],
        Contacts: [
          { required: true, message: "联系人为必填项", trigger: "blur" },
        ],
        Contactstel: [
          { required: true, message: "联系方式为必填项", trigger: "blur" },
        ],
        LegalPerson: [
          { required: true, message: "法人为必填项", trigger: "blur" },
        ],
        Tel: [
          { required: true, message: "法人联系方式为必填项", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    sure() {
      if (dialogStatus === "create") {
        this.createData();
      } else {
        this.updateData();
      }
    },
    cancel() {
      this.$emit("cancel");
    },
    createData() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          saveCompany(this.temp).then(() => {
            this.list.unshift(this.temp);
            this.dialogFormVisible = false;
            this.$notify({
              title: "Success",
              message: "Created Successfully",
              type: "success",
              duration: 2000,
            });
          });
        }
      });
    },
  },
};
</script>

