import { request } from "@/utils/request";

export function fetchList(query) {
  return request({
    url: "/Project/GetList",
    method: "get",
    params: query
  });
}

export function fetchPageList(query) {
  return request({
    url: "/Project/GetPageList",
    method: "get",
    params: query
  });
}
export function fetchListNoBundling(query) {
  return request({
    url: "/Project/GetListNoBundling",
    method: "get",
    params: query
  });
}
export function fetchProject(Id) {
  return request({
    url: "/Project/GetForm",
    method: "get",
    params: { Id }
  });
}

export function applyProject(data) {
  return request({
    url: "/Project/ApplySaveForm",
    method: "post",
    data
  });
}

export function saveProject(data) {
  return request({
    url: "/Project/SaveForm",
    method: "post",
    data
  });
}
export function unbundlingProject(data) {
  return request({
    url: "/Project/Unbundling",
    method: "post",
    data
  });
}
export function deleteProject(data) {
  return request({
    url: "/Project/DeleteForm",
    method: "post",
    data
  });
}



